<h1 id="android-linear-layout" tabindex="-1"><a class="header-anchor" href="#android-linear-layout" aria-hidden="true">#</a> Android Linear Layout</h1>
<p>Here's a short tutorial on how to use <code>LinearLayout</code> in Android.</p>
<!-- <iframe width="560" height="314" src="https://www.youtube.com/embed/nJ-9jQw69SU" frameborder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowfullscreen></iframe> -->
<h2 id="recommended-reading" tabindex="-1"><a class="header-anchor" href="#recommended-reading" aria-hidden="true">#</a> Recommended reading</h2>
<p>The reading below on this page is intended to just be an introduction to <code>LinearLayout</code>, and not a complete description of how to use all the functionality it contains. For all details about how to use it, check out <a href="https://developer.android.com/reference/android/widget/LinearLayout" target="_blank" rel="noopener noreferrer">the documentation for <code>LinearLayout</code><OutboundLink/></a>.</p>
<h2 id="linearlayout" tabindex="-1"><a class="header-anchor" href="#linearlayout" aria-hidden="true">#</a> <code>LinearLayout</code></h2>
<p>The <code>LinearLayout</code> displays all it's children next to each other, either horizontally or vertically.</p>
<h3 id="the-orientation-property" tabindex="-1"><a class="header-anchor" href="#the-orientation-property" aria-hidden="true">#</a> The <code>orientation</code> property</h3>
<p>The <code>orientation</code> property controls which direction the children should be positioned in (either <code>horizontal</code> or <code>vertical</code>).</p>
<div class="custom-container tip"><p class="custom-container-title">Example</p>
<p>Example showing how to position <code>View</code>s next to each other vertically.</p>
<CodeGroup>
