<h3 id="the-gravity-property" tabindex="-1"><a class="header-anchor" href="#the-gravity-property" aria-hidden="true">#</a> The <code>gravity</code> property</h3>
<p>The <code>gravity</code> property on <code>LinearLayout</code> controls where the children should be placed in the parent (<code>top</code>, <code>center</code>, <code>bottom</code>, etc.).</p>
