<h3 id="handling-overflow" tabindex="-1"><a class="header-anchor" href="#handling-overflow" aria-hidden="true">#</a> Handling overflow</h3>
<p>If the children can't fit inside the <code>LinearLayout</code>, the last children won't be shown (will be &quot;outside&quot; the screen).</p>
