<Smartphone>
	<div style="height: 100%; width: 100%; display: grid; grid-template-columns: max-content max-content max-content; grid-template-rows: auto 1fr; background-color: #ff0000; overflow: hidden;">
		<div style="background-color: #00ff00; grid-row: 1 / span 2">
			Hello! I am the first child.
		</div>
		<div style="background-color: #0000ff; grid-row: 1 / span 1 ">
			Hello! I am the second child.
		</div>
		<div style="background-color: #ffff00; grid-row: 1 / span 2 ">
			Hello! I am the third child.
		</div>
	</div>
</Smartphone>
</div>
<p>The best solution in this case is to put the <code>LinearLayout</code> inside a:</p>
<ul>
<li><code>ScrollView</code>, if you want the user to be able to scroll the <code>LinearLayout</code> vertically (i.e. when <code>orientation=&quot;vertical&quot;</code>)</li>
<li><code>HorizontalScrollView</code>, if you want the user to be able to scroll the <code>LinearLayout</code> (i.e. when <code>orientation=&quot;vertical&quot;</code>)</li>
</ul>
<p>And give the <code>LinearLayout</code>:</p>
<ul>
<li><code>layout_height=&quot;wrap_content&quot;</code> when <code>orientation=&quot;vertical&quot;</code></li>
<li><code>layout_width=&quot;wrap_content&quot;</code> when <code>orientation=&quot;horizontal&quot;</code></li>
</ul>
<p>The <code>layout_height</code>/<code>layout_width</code> needs to be set to <code>wrap_content</code> to allow the <code>LinearLayout</code> to grow and be as big as the children requires it to be (if it would be set to <code>match_parent</code>, it would be as tall/wide as the <code>ScrollView</code>/<code>HorizontalScrollView</code>, and you would not be able to scroll it).</p>
<div class="custom-container tip"><p class="custom-container-title">Example</p>
<p>Example showing how children don't fit in a <code>LinearLayout</code>, but thanks to a <code>HorizontalScrollView</code> can be scrolled.</p>
